.map-icons-container {
    display: flex;
    position: absolute;
    bottom: 20px;
    right: 80px;
    width: 250px;
    justify-content: space-between;
    z-index: 9;
    transition: 350ms;
}

.map-icons-right-position {
    right: 320px;
}

.white-icon {
    color: white;
}

.map-icons-container .map-individual-icon{
    font-size: 25px;
    cursor: pointer;
}

.map-icons-container .map-individual-icon-special{
    font-size: 31px;
    cursor: pointer;
    margin-top: -2px;
}

.map-icons-container .map-individual-icon-special-two{
    font-size: 28px;
    cursor: pointer;
}

.refresh-disabled-button{
    pointer-events: none;
    opacity: 0.2;
}

@keyframes flash {
    0% {
      color: green;
      box-shadow: 0 0 10px 5px rgba(0, 255, 0, 0.7);
    }
    50% {
      color: inherit; /* or the default color of the icon */
      box-shadow: none;
    }
    100% {
      color: green;
      box-shadow: 0 0 10px 5px rgba(0, 255, 0, 0.7);
    }
  }
  
.flash-icon {
    animation: flash 2s infinite; /* Adjust the duration as needed */
}