.mapgeosearch-container {
    display: flex;
    justify-content: center;
    z-index: 8;
    position: absolute;
    top: 30px;
    width: 450px;
}

.mapgeosearch-all-elems {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.mapgeosearch-input-flex {
    display: flex;
    flex-direction: row;
    width: 100%;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
}

.mapgeosearch-input{
    display: block;
    width: 412px;
    padding: 7px 12px;
    border: none;
    background-color: rgba(255, 255, 255, 0.5);
}

.mapgeosearch-searchbtn{
    width: 38px;
    margin-left: 0px;
    padding: 7px 12px;
    background-color: #242424;
    color: white;
    border: none;
    cursor: pointer;
}

.mapgeosearch-list{
    display: block;
    width: 412px;
}

.mapgeosearch-list-item{
    padding: 7px 15px;
    color: black;
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: rgb(228, 228, 228);
    border: none;
}

.mapgeosearch-list-item:hover{
    background-color: aliceblue;
    transition: 250ms;
    cursor: pointer;
}

.mapgeosearch-list-divider {
    border-top: 1px solid #bbb;
}

.mapgeosearch-list-item-icon{
    margin-right: 15px;
}

.mapgeosearch-list-item-text{
    font-size: 12px;
}
