:root {
    --primary: #fff;
}

.btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--outlinefull {
    background-color: transparent;
    /*color: #fff;*/
    color: black;
    padding: 8px 20px;
    width: 70%;
    /*border: 1px solid var(--primary);*/
    border: 5px solid black;
    transition: all 0.3s ease-out;
}

.btn--customized {
    display: block;
    /*background-image: linear-gradient(to bottom, rgb(211, 212, 226), rgb(181, 182, 206));*/
    background-color: rgb(222, 222, 222);
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    /*border: 1px solid black;*/
    text-transform: uppercase;
    /*text-decoration: underline;*/
    /*border: 1px dashed black;*/
    color: black;
    transition: all 0.3s ease-out;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5); /* Add a subtle box shadow for the 3D effect */
    /*text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);*/
}

.btn--medium {
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--super {
    padding: 4px 22px;
    font-size: 18px;
}

.btn--medium:hover, .btn--large:hover {
    /*background: #fff;*/
    /*color: #242424;*/
    background: #242424;
    color: #fff;
    transition: all 0.3s ease-out;
}

.btn--super:hover {
    /*background-image: linear-gradient(to bottom, rgb(197, 198, 212), rgb(132, 134, 165));*/
    background-color: #242424;
    color:#fff;
    transition: all 0.3s ease-out;
}

