.circleMarkerBoxContainer {
    background-color: aliceblue;
    width: 300px;
    padding: 10px 10px;
    position: absolute;
    bottom: 70px;
    border-radius: 10px;
    z-index: 9;
    right: 80px;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
    transition: 350ms; 
}

.circleMarkerBoxContainer-right-position {
    right: 320px;
}

.circleMarkerBoxContainer .tab-box-left {
    display: flex;
    width: 10%;
    align-items: center;
    justify-content: center;
}

.circleMarkerBoxContainer .tab-box-right {
    display: flex;
    width: 10%;
    align-items: center;
    justify-content: center;
}

.circleMarkerBoxContainer .tab-box {
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.circleMarkerBoxContainer .tab-box .info-tab-button-one {
    padding: 5px;
    min-width: 70px;
    border-radius: 5px;
    border-top: 2px solid black;
    border-bottom: 2px solid black;
    border-left: 2px solid black;
    border-right: 2px solid black;
    background-color: transparent;
    color: black;
    cursor: pointer;
}

.circleMarkerBoxContainer .tab-box .info-tab-button-two {
    padding: 5px;
    min-width: 70px;
    border-top-right-radius: 7px; 
    border-bottom-right-radius: 7px;
    border-top: 2px solid black;
    border-bottom: 2px solid black;
    border-right: 2px solid black;
    background-color: transparent;
    color: black;
    cursor: pointer;
}

.circleMarkerBoxContainer .tab-box .info-tab-button-one:hover {
    background-color: lightgrey;
    color: white;
    transition: all 0.3s ease-out;
}

.circleMarkerBoxContainer .tab-box .info-tab-button-two:hover {
    background-color: lightgrey;
    color: white;
    transition: all 0.3s ease-out;
}

.circleMarkerBoxContainer .box-container-top-section{
    margin-bottom: 15px;
    border-bottom: 2px solid black;
}

.circleMarkerBoxContainer .column-divider {
    display: flex;
    flex-direction: column;
}

.circleMarkerBoxContainer .column-divider .row-wrapper{
    display: flex;
    margin-bottom: 5px;
}

.circleMarkerBoxContainer .column-divider .row-wrapper-delta{
    display: flex;
    margin-bottom: 10px;
}

.circleMarkerBoxContainer .column-divider .row-wrapper .box-title{
    font-size: 13.5px;
    font-weight: bold;
    text-decoration: underline;
}

.circleMarkerBoxContainer .column-divider .row-wrapper-delta .box-title{
    font-size: 13.5px;
    font-weight: bold;
    text-decoration: underline;
}

.leftBoxContainer {
    display: flex;
    width: 25%;
    align-items: center;
    justify-content: center;
}

.middleBoxContainer {
    display: flex;
    width: 45%;
    align-items: center;
    justify-content: center;
}

.rightBoxContainer {
    display: flex;
    width: 30%;
    align-items: center;
    justify-content: center;
}

.freq-font{
    font-size: 12px;
}

.radios-value-font{
    font-size: 12px;
}

.radios-number-font{
    font-size: 12px;
}

.circleMarkerBoxContainer .top-closing-container{
    display: flex;
    width: 100%;
    margin-bottom: 10px;
}

.circleMarkerBoxContainer .top-closing-container .box-closing-icon{
    font-size: 20px;
    margin-left: 0px;
    cursor: pointer;
}

.circleMarkerBoxContainer .data-row-wrapper{
    display: flex;
    margin-bottom: 5px;
}

.circleMarkerBoxContainer .data-box-title{
    font-size: 13.5px;
    font-weight: bold;
    text-decoration: underline;
    margin-right: 20px;
}

.circleMarkerWarning {
    background-color: aliceblue;
    width: 250px;
    height: 40px;
    padding: 10px 10px;
    position: absolute;
    bottom: 70px;
    border-radius: 10px;
    z-index: 9;
    right: 80px;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
    transition: 350ms;
}

.circleMarkerWarning-right-position {
    right: 320px;
}

.circleMarkerWarning .warning-closing-container{
    width: 100%;
    margin-bottom: 0px;
}

.circleMarkerWarning .warning-closing-container .warning-closing-icon{
    position: absolute;
    right: 10px;
    font-size: 15px;
    cursor: pointer;
}

.mapinfo-divider{
    border-top: 2px solid black;
    margin-top: 10px;
}

.showSunburst-wrapper{
    display: flex;
    margin-top: 15px;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.showSunburst{
    background-color: rgb(23, 175, 23);
    color: #FFFFFF;
    font-size: 12px;
    font-weight: bold;
    padding: 5px 10px;
    border:none;
    cursor: pointer;
    transition: 0.3s;
}

.showSunburst:hover {
    background-color: rgb(43, 230, 43);
    box-shadow: 6px 8px 10px rgba(0, 0, 0, 0.4);
    transition: 250ms;
}

.showSunburst:active {
    transform: scale(0.95);
}

.showSunburst:disabled {
    background-color: lightgray; 
    color: gray; 
    cursor: not-allowed; 
}