.ratetheapp{
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center;
    background-image: url('/public/images/ericsson_background_grey2.jpg');
    overflow-anchor: none;
}

.ratetheapp {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    /*color: #fff;*/
    color: black;
}

.rate-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.rate-h2 {
    font-size: 50px;
    margin-bottom: 15px;
}

.rate-data-issue {
    font-size: 20px;
    margin-bottom: 15px;
    margin-right: 180px;
}

.rate-stars {
    display: flex;
    flex-direction: row;
    margin-bottom: 15px;
}

.rate-textarea {
    /*border: 1px solid #a9a9a9;*/
    border: 1px solid black;
    border-radius: 5px;
    padding: 10px;
    min-height: 100px;
    min-width: 460px;
    margin-bottom: 30px;
    color: white;
    background-color: black;
}

.rate-textarea-dataissue {
    /*border: 1px solid #a9a9a9;*/
    border: 1px solid black;
    border-radius: 5px;
    padding: 10px;
    min-height: 100px;
    min-width: 460px;
    margin-bottom: 30px;
    color: white;
    background-color: black;
}

.rate-button {
    border: 1px solid #a9a9a9;
    border-radius: 5px;
    width: 230px;
    padding: 10px;
}