.tooltip-container {
    position: relative;
    display: inline-block;
}

.tooltip-container .tooltip {
    position: absolute;
    top: calc(-100% - 20px);
    transform: translateX(-65%);
    background-color: rgb(52, 52, 52);
    color: white;
    padding: 8px;
    border-radius: 4px;
    z-index: 11;
    font-size: 10px;
    min-width: 200px;
    border: 1px solid white; 
}
