.leaflet-container {
    height: 100%;
    margin-left: 60px;
    margin-right: 60px;
}

.leaflet-control-scale {
    color: #333; 
    padding: 10px; 
    font-size: 15px; 
    font-weight: bold;
}

.polyline-measure-unicode-icon, .polyline-measure-clearControl, #unitControlId{
    cursor: pointer;
}

.map{
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center;
}

.map-wrapper{
    position: fixed; /* or 'fixed' depending on your use case */
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    z-index: 1;
}

.loaderText{
    font-size: 30px;
    /*color: #36d7b7;*/
    color: black;
    margin-top: 30px;
    z-index: 2;
}

.loading-box-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background-color: azure;
}

.line-loader{
    width: 25%;
    margin-top: 50px;
}

.progress-text {
    font-size: 20px;
    color: black;
    margin-top: 30px;
}