.settings-page{
    min-height: 100vh;
    background-color: rgb(231, 231, 231);
}

.settings-page-shift{
    display:flex;
    align-items: center;
    flex-flow: column;
}

.settings-container {
    display: block;
    width: 750px;
    margin-top: 60px;
}

.settings-title-wrapper {
    display: block;
    padding: 20px 20px 0px 20px;
    display: flex;
    margin-top: 50px;
}

.settings-font-size {
    font-size: 2.5rem;
}

.settings-font-size-icon {
    font-size: 2.5rem;
    margin-top: 3px;
    margin-right: 10px;
}

.settings-column-wrapper {
    background-color: transparent;
}

.settings-row-wrapper {
    display: flex;
    width: 100%;
    padding: 20px;
    justify-content: space-between;
    background-color: rgb(245, 245, 245);
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5);
}

.settings-text-wrapper {
    margin-left: 0px;
    font-size: 1.2rem;
}

.settings-row-divider {
    border-top: 30px solid transparent;
}

.setting-text-input {
    height: 27.2px;
    width: 80px;
    border: none;
    background-color: grey;
    padding: 5px;
    color: white;
    font-weight: bold;
}

.switch {
    position: relative;
    display: inline-block;
    width: 48px;
    height: 27.2px;
  }
  
.switch input { 
    opacity: 0;
    width: 0;
    height: 0;
}
  
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
}
  
.slider:before {
    position: absolute;
    content: "";
    height: 20.8px;
    width: 20.8px;
    left: 3.2px;
    bottom: 3.2px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
}
  
input:checked + .slider {
    background-color: #2196F3;
}
  
input:focus + .slider {
    box-shadow: 0 0 0.8px #2196F3;
}
  
input:checked + .slider:before {
    -webkit-transform: translateX(20.8px);
    -ms-transform: translateX(20.8px);
    transform: translateX(20.8px);
}

.settings-button-wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 60px;
}

.settings-button-save {
    display: inline-block; /* or use 'block' if it's a block-level element */
    padding: 15px 20px;
    color: black;
    border: none;
    background-color: lightgrey;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5);
    font-size: 1rem;
    cursor: pointer;
}

.settings-button-save:hover {
    color: white;
    background-color: grey;
    transition: all 0.3s ease-out;
}
  
